@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

nav {
    height: 65px;
    background: #455560;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    z-index: 99;
    margin-bottom: 40px;
}

nav .title {
    font-size: 35px;
    color: #fafafb;
    margin-left: 25px;
}
nav i {
    color: red;
}

.container {
    max-width: 1600px;
    width: 100%;
    margin: auto;
}

.country {
    display: flex;
    display: -webkit-flex;
    -webkit-align-items: center;
            align-items: center;
    height: 70px;
}

.country img {
    margin-right: 20px;
}

.country-name {
    font-size: 2.5rem;
    font-weight: 700;
    text-transform: uppercase;
}

.box {
    background-color: #ffffff;
    padding: 20px;
    border: 0.5px solid #e9e9e9;
    border-radius: 15px;
    position: relative;
    box-shadow: 5px 5px 10px #c1c2c4;
    overflow: auto;
}

.box-header {
    text-transform: capitalize;
    font-size: 1.25rem;
    font-weight: 700;
}

.box-body {
    padding: 20px 0;
}

.confirmed-text {
    color: #ff0000;
}

.recovered-text {
    color: #008000;
}

.deaths-text {
    color: #373c43;
}

.counter {
    /* color: #fff; */
}

.counter .counter-title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 20px;
    text-transform: capitalize;
}

.counter .counter-infor {
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 2rem;
    font-weight: 700;
}
.counter .counter-infor i {
    /* width: 50px;
    height: 50px; */
}

table {
    width: 100%;
    min-width: 100%;
    border-collapse: separate;
    border-spacing: 10px 0;
}

.table-spacing{
    border-spacing: 5px;
}

table thead tr {
    text-align: left;
}

table th,
table td {
    padding: 10px 0;
    border-bottom: 1px solid #e9e9e9;
}

table tr {
    height: 55px;
}

table td > div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

table td img {
    margin-right: 20px;
}

.pagination {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.pagination .page-item {
    height: 40px;
    width: 40px;
    /* display: flex; */
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 5px;
    visibility: hidden;
    opacity: 0;
    display: none;
}

.pagination .page-item.show{
    visibility: visible;
    opacity: 1;
    display: -webkit-flex;
    display: flex;
}

.page-item:first-child > .page-link,
.page-item:last-child > .page-link{
    font-size: 25px;
}
.pagination .page-item:hover {
    background: rgb(226, 226, 226);
}
.pagination .page-item.active {
    background: #4caf50;
}
.pagination .page-item .page-link {
    text-align: center;
    line-height: 40px;
    width: 100%;
    color: #373c43;
}

.page-item.active > .page-link {
    color: white;
}
.page-link > a {
    width: 100%;
}
.box .country-menu {
    max-height: 396px;
    overflow-y: auto;
}

a {
    text-decoration: none;
}

.box .country-menu a:first-child{
    margin-bottom: 6px;
}
.box .country-menu > a > img {
    object-fit: fill;
    height: 20px;
    width: 32px;
}
.box .country-menu a,
.box .country-menu a > div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.box .country-menu a > div > img,
.box .country-menu > a > img {
    margin-right: 20px;
}


.box::-webkit-scrollbar,
.box .box-body::-webkit-scrollbar,
.box .country-menu::-webkit-scrollbar {
    width: 0.5em;
    height: 0.5em;
}

.box::-webkit-scrollbar-thumb,
.box .box-body::-webkit-scrollbar-thumb,
.box .country-menu::-webkit-scrollbar-thumb {
    background-color: #b3b7bb;
    border-radius: 5px;
}

.box::-webkit-scrollbar-track,
.box .box-body::-webkit-scrollbar-track,
.box .country-menu::-webkit-scrollbar-track {
    background: #dfe1e2;
    border-radius: 5px;
}


.sticky-sidebar {
    top: 10px;
    position: -webkit-sticky;
    position: sticky;
    z-index: 97;
}

.loader {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: grid;
    place-items: center;
    background-color: #ffffff;
    font-size: 10rem;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s ease-in-out;
}

.loading .loader {
    visibility: visible;
    opacity: 1;
}


.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 0;
    padding: 7px;
}

.p-0 {
    padding: 0 !important;
}
.p-1 {
    padding: 10px !important;
}
.p-2 {
    padding: 20px !important;
}
.p-3 {
    padding: 30px !important;
}
.p-4 {
    padding: 40px !important;
}

.m-0 {
    margin: 0 !important;
}
.m-1 {
    margin: 10px !important;
}
.m-2 {
    margin: 10px !important;
}
.m-3 {
    margin: 10px !important;
}
.m-4 {
    margin: 10px !important;
}

.mr-05 {
    margin-right: 5px;
}
.mf-05 {
    margin-left: 5px;
}

.f-width {
    width: 100%;
}

.f-height {
    height: 100%;
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 15px;
}

